@font-face {
  font-family: ZillaSlab-Bold;
  src: local('ZillaSlab-Bold'), url(./fonts/Zilla_Slab/ZillaSlab-Bold.ttf) format('opentype');
  /* src: url(./fonts/Novus-Regular.otf) format('opentype'); */
  /* src: url("https://www.zhuliming.cn/static/font/Novus-Regular.otf") format('opentype'); */
}
@font-face {
  font-family: ZillaSlab-Medium;
  src: local('ZillaSlab-Medium'), url(./fonts/Zilla_Slab/ZillaSlab-Medium.ttf) format('opentype');
}
@font-face {
  font-family: ZillaSlab-Regular;
  src: local('ZillaSlab-Regular'), url(./fonts/Zilla_Slab/ZillaSlab-Regular.ttf) format('opentype');
}




.App {
  margin: 0 auto;
  margin-top: 30px;
  width: 1200px;
  background-color: #fff;
  /* border: 1px solid #DDD; */
  /* border-radius: 10px; */
}

.App * {
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.no-select {
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
}

.tone_header {
  position: relative;
  left: 120px;
  display: flex;
  margin-bottom: 40px;
}

.tone_header_item {
  /* background-color: #F5F6F7; */
  width: 80px;
  height: 80px;
  justify-content: center;
  align-items: center;
  display: flex;
  font-size: 18px;
  color: #666;
}
.tone_active {
  font-size: 36px;
  color: green;
  border-radius: 40px;
  font-weight: bold;
  background-color: #DDD;
}

.scales_container {
  margin-bottom: 200px;
}

.scale_container {
  position: relative;
  display: flex;
  /* margin-bottom: 10px; */
  /* margin-top: 10px; */
  height: 80px;
  /* border-top: 1px solid #999; */
}


.scale_name {
  /* background-color: red; */
  color: #333;
  width: 120px;
  text-align: center;
  font-size: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.scale_number {
  display: flex;
  width: 80px;
  height: 80px;
  font-weight: bold;
  /* border: 1px solid #999; */
  outline: 0.5px solid #EEE;
  /* background-color: red; */
  justify-content: center;
  align-items: center;
  font-size: 64px;
  color: #EEE;
  color: #FFF;
}

.scale_code {
  position: absolute;
  left: 120px;
  top: 0;
  width: calc(1200px - 120px);
  height: 80px;
  /* background-color: red; */
}
.scale_code_txt {
  position: absolute;
  font-size: 30px;
  display: flex;
  height: 80px;
  width: 80px;
  justify-content: center;
  align-items: center;
}

.code_container {
  display: flex;
  align-items: center;
}

.code_txt {
  color: #333;
  font-size: 24px;
  /* font-weight: bold; */
}

.code_sub_txt {
  margin-left: 4px;
  margin-top: 6px;
  color: #999;
  font-size: 16px;
}